div,h1{color:#fff}

.discover-item{position:relative}.discover-item__image{height:200px;border-radius:10px;border:0.7px solid #42a5f5;opacity:0.6;-webkit-transition:200ms;transition:200ms}.discover-item :hover{opacity:1}.discover-item__rating{position:absolute;left:1px;top:0;background:#ccc;color:#1C262B;padding:5px;opacity:0.8}.discover-item__title{color:#ccc}

.discover-container{background:#1C262B}.discover-container h2{text-align:center;padding-top:20px}.discover-container-header{display:flex;flex-direction:column;max-width:80%;margin:0 auto;padding:20px;background:linear-gradient(150deg, rgba(40,207,219,0.142) 30%, rgba(52,238,238,0.342) 62%, rgba(13,150,204,0.142) 92%);border-radius:20px}.discover-container-header form{margin:0 auto}.discover-container-header select,.discover-container-header input{background-color:#445d69;border:none;border-radius:5px;color:#fff;padding:0.3rem}.discover-container-header button{height:32px;width:50%;margin:0 auto;border-radius:10px;color:rgba(219,69,69,0.9);background:#000;font-family:"Roboto", sans-serif;font-weight:700;letter-spacing:6.5px;cursor:pointer}.discover-container-header__filters{width:250px;display:flex;flex-direction:column;justify-content:space-around;height:120px}.discover-container-body{display:grid;grid-template-columns:repeat(auto-fit, 160px);grid-gap:4rem;justify-items:center;justify-content:center;padding-top:50px;border-radius:15px}.discover-container-body a{text-decoration:none}

